
import Collapse from "@/components/Collapse/Collapse";
import showMore from '@/components/ShowMore/showMore' 
import XHeader from 'vux/src/components/x-header/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import {
setTimeout
} from 'timers';
export default {
components: {
XHeader,
InlineLoading,
showMore,
Collapse
},
beforeRouteLeave(to, from, next) { // 用于做缓存

if (to.name == 'targetdetail') {

localStorage.setItem('lastcodetarget', this.$route.params.code || window.localStorage.lastcodetarget)
} else {
localStorage.removeItem('lastcodetarget');
}
next();
},
data() {
return {
lastcode: '',
list: [],
close: false
};
},
mounted() {
this.lastcode = this.$route.params.code || window.localStorage.lastcodetarget;
this.initdata();
},
methods: {
initdata() {
this.$ajax.post('', {
'MSH.1': 'tjjl',
'MSH.2': 'qryExceptionList',
'TJJLID': this.lastcode,
'token':this.$root.loginInfo.token,
}).then(data => {
console.log(data)
if (typeof data != 'undefined' && Object.keys(data).length > 0) {
this.list = data;
}
})
},
cancerDetail(name, code) { //点击跳转
this.$router.push({
name: "targetdetail",
params: {
code: code,
name: name,
}
});
},
}
};



